import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { PlacesService } from '../../places.service';
import { Router } from '@angular/router';
import { LoadingController } from '@ionic/angular';
import { PlaceLocation } from '../../location.model';

@Component({
  selector: 'app-new-offer',
  templateUrl: './new-offer.page.html',
  styleUrls: ['./new-offer.page.scss']
})
export class NewOfferPage implements OnInit {
  newOfferForm: FormGroup;
  constructor(
    private placesService: PlacesService,
    private router: Router,
    private loadingCtrl: LoadingController
  ) {}

  ngOnInit() {
    this.newOfferForm = new FormGroup({
      title: new FormControl(null, {
        updateOn: 'blur',
        validators: [Validators.required]
      }),
      description: new FormControl(null, {
        updateOn: 'blur',
        validators: [Validators.required, Validators.maxLength(180)]
      }),
      price: new FormControl(null, {
        updateOn: 'blur',
        validators: [Validators.required, Validators.min(1)]
      }),
      dateFrom: new FormControl(null, {
        updateOn: 'blur',
        validators: [Validators.required]
      }),
      dateTo: new FormControl(null, {
        updateOn: 'blur',
        validators: [Validators.required]
      }),
      location: new FormControl(null, {
        validators: [Validators.required]
      })
    });
  }
  onLocationPicked(loc: PlaceLocation) {
    this.newOfferForm.patchValue({location: loc});
  }
  onImagePicked(imageData: string) {

  }
  onCreateOffer() {
    if (!this.newOfferForm.valid) {
      return;
    }
    const frmData = this.newOfferForm.value;
    this.loadingCtrl
      .create({ message: 'creating place...' })
      .then(loadingEl => {
        loadingEl.present();
        this.placesService
          .addPlace(
            frmData.title,
            frmData.description,
            +frmData.price,
            new Date(frmData.dateFrom),
            new Date(frmData.dateTo),
            frmData.location
          )
          .subscribe(() => {
            loadingEl.dismiss();
            this.newOfferForm.reset();
            console.log('creating offered place: ', frmData);
            this.router.navigate(['/places/tabs/offers']);
          });
      });
  }
}
